
import Previewer from 'vux/src/components/previewer/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import currDatas from './data.js'
export default {
mounted() {
this.$store.commit('UPDATE_PAGE_TITLE', '最新活动')
setTimeout(() => {
this.$refs.tabBox.$children[0].onItemClick()
}, 200)
},
data(){
return {
photos: currDatas.photoes,
currTabIdx: 0,
photoShow: false,
currentPhoto: '',
dialogStyle: {
width: '200px',
height: '200px'
},
previews: [{
src: 'https://placekitten.com/800/400',
w: 600,
h: 400
},
{
src: 'https://placekitten.com/1200/900',
msrc: 'https://placekitten.com/120/90', // 缩略图
w: 1200,
h: 900
}]
}
},
methods:{
tabClick(idx) {
this.currTabIdx = idx;
},
clickPhoto(idx) {

this.currentPhoto = this.photos[idx];
this.photoShow = true;
},
closePhotoDlg() {
this.photoShow = false;
}
},
components: {
Tabbar,
Previewer ,
TabbarItem,
XHeader,
XButton,
XImg,
Tab,
TabItem,
XDialog,
Divider,
Card
}
}




